	font-family: 'Inter';
	src: url(${___CSS_LOADER_URL_REPLACEMENT_0___})
		format('truetype');
}

:root {
	--stroke-soft: #f2f2f5;
	--base-higher-p: #fff;
	--base-white: #fff;
	--border-red: #ff3b3b;
	--border-orange: #ff8800;
	--border-blue: #3e7bfa;
	--gradient-red: linear-gradient(90deg, #ff3b3b, #cc00bb);
	--gradient-orange: linear-gradient(90deg, #ff8800, #ee5d5d);
	--gradient-blue: linear-gradient(90deg, #3e7bfa, #6600cc);
}
* {
	box-sizing: border-box;
}

*:focus-visible {
	outline: 0.5px rgba(150, 150, 150, 0.23) solid;
	box-shadow: inset 0px 0px 20px 0px #8080803b;
	border-radius: 3px;
}

html {
	font-family: 'Inter', sans-serif;
	font-size: 16px;
	background: #fafafc;
}

header {
	display: flex;
	justify-content: center;

	gap: 1.7rem;
	padding-top: 2rem;
}

/* body {
	height: 100%;
} */

p {
	margin: 0;
}

.container {
	position: relative;
	height: 100%;
}

/* Add TODO BTN */
.cancelForm:hover,
.deleteTodo:hover,
.deleteTodoEdit:hover,
.cancelListForm:hover {
	/* ! UPDATE COLOURS */
	background: #ff5d5d;
	color: #700;
}

.cancelForm,
.deleteTodo,
.deleteTodoEdit,
.cancelListForm {
	width: 0.8rem;
	height: 0.8rem;
	align-items: center;
	justify-self: center;

	background-color: var(--base-higher-p, #fff);
	border: 1px solid #dadadd;
	border-radius: 100%;
	filter: drop-shadow(0px 20px 32px rgba(96, 97, 112, 0.24))
		drop-shadow(0px 2px 8px rgba(40, 41, 61, 0.08));

	font-size: 0.6rem;
	text-align: center;
	color: transparent;
}

.addTodoBtn {
	z-index: 10;

	display: flex;
	justify-content: center;
	align-items: center;

	height: 3.125rem;
	width: 3.125rem;
	padding-bottom: 0.4rem;

	border: 1px solid var(--stroke-soft, #f2f2f5);
	border-radius: 3.125rem;
	background: var(--base-white, #fff);

	box-shadow: 0px 4px 8px 0px rgba(96, 97, 112, 0.16),
		0px 0px 2px 0px rgba(40, 41, 61, 0.04);

	font-family: Inter, sans-serif;
	color: #1c1c28;
	font-feature-settings: 'clig' off, 'liga' off;
	font-size: 2.25rem;
	font-style: normal;
	font-weight: 200;
	line-height: 1.5rem;

	cursor: pointer;
}

.hamburger {
	z-index: 10;

	display: flex;
	width: 6.25rem;
	height: 3.1875rem;
	justify-content: center;
	align-items: center;

	border-radius: 1.25rem;
	border: 1px solid var(--stroke-soft, #f2f2f5);
	background: var(--base-white, #fff);
	box-shadow: 0px 4px 8px 0px rgba(96, 97, 112, 0.16),
		0px 0px 2px 0px rgba(40, 41, 61, 0.04);

	font-family: Inter, sans-serif;
	color: #1c1c28;
	font-feature-settings: 'clig' off, 'liga' off;
	font-size: 1.2rem;
	font-style: normal;
	font-weight: 300;
	line-height: 1.5rem;

	cursor: pointer;
}

.addTodoBtn:hover,
.hamburger:hover {
	border: 2px solid #ebebf0;
}

.addTodoBtn:active,
.hamburger:active {
	border: 3px solid #e4e4eb;
}

.sidebar {
	z-index: 10;
	display: flex;
	flex-direction: column;
	gap: 30px;

	width: 11rem;

	transition: transform 0.3s;
	/* transform: translateX(-150%); */

	position: fixed;
	top: 6rem;
	left: 4rem;
	right: 0;
	bottom: 0;
}

.showSidebar {
	/* transform: translateX(0); */
	transform: translateX(-150%);
}

.defaultSideLists,
.customSideLists {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;

	width: 11rem;
	padding: 0.625rem 1.375rem;

	border-radius: 1.25rem;
	border: 1px solid var(--stroke-soft, #f2f2f5);
	background: var(--base-white, #fff);

	box-shadow: 0px 4px 8px 0px rgba(96, 97, 112, 0.16),
		0px 0px 2px 0px rgba(40, 41, 61, 0.04);

	padding: 0.62rem 1.38rem;
}

.defaultSideLists {
	height: 8.75rem;
}

.defaultSideLists > .sidebarButton:last-child,
.customSideLists > .sidebarButton:last-child {
	border: 0;
}

.sidebarButton {
	width: 8.25rem;
	height: 2.5rem;

	border: 0;
	border-bottom: 1px solid #ebebf0;
	background: transparent;

	text-align: left;

	font-family: Inter, sans-serif;
	color: #1c1c28;
	font-size: 1rem;
	font-style: normal;
	font-weight: 300;
	line-height: 1.5rem;
	text-overflow: ellipsis;

	cursor: pointer;
}

/** LIST */

.list {
	display: flex;
	flex-direction: column;
	align-items: center;

	/* height: 100%; */
	width: clamp(25rem, 40rem, 55rem);
	margin: auto;
	margin-top: 3.7rem;
}

.listHead {
	width: 100%;
	height: 6.875rem;
	align-self: baseline;
	margin: 0 2.4rem;
}

.listTitle,
.customTitle {
	/* display: flex; */
	flex-direction: column;
	justify-content: center;

	height: 50%;

	letter-spacing: 0.1rem;

	font-family: Inter, sans-serif;
	color: var(--dark-dark-0, #1c1c28);
	font-size: 2.3rem;
	font-style: normal;
	font-weight: 300;
	line-height: normal;

	text-overflow: ellipsis;
}

.listDescription,
.customDescription {
	display: flex;
	height: 50%;

	font-family: Inter, sans-serif;
	color: rgba(28, 28, 40, 0.22);
	font-feature-settings: 'clig' off, 'liga' off;
	font-size: 1rem;
	font-style: normal;
	font-weight: 400;
	line-height: 1.5rem;

	text-overflow: ellipsis;
}

.customTitle,
.customDescription {
	background: transparent;
	border: 0;
}

.completedTodosTitle {
	padding: 0.4rem 0;
	border-bottom: 1px solid rgba(185, 185, 185, 0.423);

	font-size: 0.9rem;
	font-family: Inter, sans-serif;
	color: rgba(28, 28, 40, 0.22);
	font-weight: 300;
	letter-spacing: 0.5px;
}

.activeTodos,
.completedTodos {
	/* ! MASK OVERFLOW fade */
	display: flex;
	flex-direction: column;
	align-items: center;

	width: 100%;

	gap: 0.4rem;

	padding-top: 10px;
	padding-bottom: 20px;

	overflow: scroll;
}

.activeTodos {
	max-height: 50vh;
}

.completedTodos {
	max-height: 20vh;
}

/* TODO */

/* TODO FORM */

#todoForm {
	z-index: 1;
	position: absolute;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);

	display: grid;
	grid-template-rows: repeat(4, 2.5rem);
	grid-template-columns: 1fr 1fr 1fr;

	width: 400px;
	margin: auto;
	padding: 15px 13px;

	border-radius: 20px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	text-overflow: ellipsis;
}

.cancelForm {
	position: absolute;
	top: 5px;
	left: 10px;
}

#formTitle,
#formNotes,
#formDate,
#formList,
#formPriority {
	height: 100%;
	width: 100%;
	padding: 5px 5px;

	border: 0;
	border-bottom: 1px solid #a1a2a430;

	background: transparent;
}

.formTitle {
	grid-area: 1 / 1 / 1 / 4;
	align-self: center;
}

#formTitle {
	width: 95%;
	height: 60%;
	padding: 5px 10px;
	text-overflow: ellipsis;
}

.formNotes {
	grid-area: 2 / 1 / 4 / 4;
	align-self: center;

	height: 100%;
	width: 100%;
	margin-bottom: 3px;

	border-radius: 10px;
	border: 1px solid #a1a2a430;
}

#formNotes {
	padding: 5px 10px;
	border: 0;
}

textarea {
	resize: none;
	overflow: scroll;
}

.formDate,
.formList,
.formPriority {
	align-self: end;
	justify-self: center;
}

.formDate {
	grid-area: 4 / 1;
}

#formDate {
	width: 90%;
}

.formList {
	grid-area: 4 / 2;
}

#formList {
	width: 100%;
	padding: 5px;
}

.formPriority {
	grid-area: 4 / 3;
}

#priority {
	width: 100%;
}

.placeholderPri {
	color: gray;
}

.submitButton {
	position: absolute;
	bottom: -2.5rem;

	height: 1.8rem;
	width: 6.5rem;

	border: 1px solid #808080b8;
	border-radius: 10px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;

	align-self: end;
	justify-self: center;
}

/* TODO CARD */

.todoCard {
	position: relative;

	display: flex;
	column-gap: 10px;

	width: clamp(20rem, 35rem, 35rem);
	height: 2.6875rem;

	margin: 0.5rem 0.5rem 0rem;
	padding: 10px 5px;

	border-radius: 0.9375rem;
	background-clip: padding-box;

	box-shadow: 0px 8px 16px 0px rgba(96, 97, 112, 0.16),
		0px 2px 4px 0px rgba(40, 41, 61, 0.04);

	cursor: pointer;
}

.todoCheck {
	margin-left: 10px;
}

.todoDate {
	font-size: 0.7rem;
	color: gray;
	align-self: end;
}

.deleteTodo {
	/* position: absolute;
	top: 30%;
	right: 10px; */
}

.todoCheck {
	/* margin-left: 10px; */
	grid-area: 1 / 1 / 3 / 2;
	justify-self: center;
}

/* TODO CARD - edit */

.editCard {
	position: relative;

	display: grid;
	grid-template-columns: 0.1fr 0.5fr 0.5fr 0.5fr;
	grid-template-rows: 1fr 1fr 1fr 1fr;

	width: 90%;
	padding: 10px 5px;
	margin: 0.5rem 0.5rem 0rem;

	border-radius: 20px;
	border: 1px solid gray;
	border-bottom: 5px solid #8080809c;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 3px 5px 12px 0px #00000042;
}

.deleteTodoEdit {
	position: absolute;
	right: 10px;
	top: 10px;
}

.todoTitleEdit,
.todoDueDateEdit,
.todoNotesEdit,
.todoPriorityEdit {
	background: transparent;
	border: 0;
	text-overflow: ellipsis;
}

.todoNotesEdit,
.todoDueDateEdit,
.todoPriorityEdit {
	font-size: 0.8rem;
}

.todoTitle {
	max-height: 1.1rem;
	overflow: auto;
	max-width: 75%;
}

.todoCheckEdit {
	grid-row: 1 / span 4;
	align-self: flex-start;
	margin-top: 10%;
	border-radius: 10px;
}

.todoTitleEdit {
	grid-column: 2 / span 3;
}

.todoNotesEdit {
	grid-column: 2 / span 3;
	grid-row: 2 / span 2;
	width: 95%;
}

.todoDueDateEdit {
	grid-column: 2;
	grid-row: 4;

	justify-self: start;
	width: 70%;
}

.todoListEdit {
	grid-column: 3;
	grid-row: 4;
	justify-self: start;

	border: 0;
	background: transparent;

	font-size: 0.9rem;
}

.todoPriorityEdit {
	grid-column: 4;
	grid-row: 4;
	align-self: end;
	justify-self: start;

	width: 85%;
}

.done > * {
	color: #8080807d;
	text-decoration: line-through;
	color: rgba(128, 128, 128, 0.64);
}

/* Priority */
.low {
	background: linear-gradient(var(--base-white), var(--base-white)) padding-box,
		var(--gradient-blue) border-box;
	border: 1px solid;
	border-bottom: 2px solid;
	border-color: transparent;
	box-shadow: 0px 8px 16px 0px rgba(62, 123, 250, 0.35),
		0px 2px 4px 0px rgba(62, 123, 250, 0.37);
}

.medium {
	background: linear-gradient(var(--base-white), var(--base-white)) padding-box,
		var(--gradient-orange) border-box;
	border: 0.1rem solid;
	border-bottom: 2px solid;
	border-color: transparent;
	box-shadow: 0px 8px 16px 0px rgba(255, 136, 0, 0.23),
		0px 2px 4px 0px rgba(255, 136, 0, 0.4);
}

.high {
	background: linear-gradient(var(--base-white), var(--base-white)) padding-box,
		var(--gradient-red) border-box;
	border: 0.1rem solid;
	border-bottom: 2px solid;
	border-color: transparent;
	box-shadow: 0px 8px 16px 0px rgba(255, 59, 59, 0.35),
		0px 2px 4px 0px rgba(255, 59, 59, 0.69);
}

.none {
	background: var(--base-white, #fff);
	border: 1px solid #e4e4eb;

	box-shadow: 0px 8px 16px 0px rgba(96, 97, 112, 0.16),
		0px 2px 4px 0px rgba(40, 41, 61, 0.04);
}

/* displayChange */
.changeAlert {
	background-color: black;
	color: whitesmoke;
	transform: translateY(-150%);
	transition: transform 0.6s;
	width: 200px;
	z-index: 10;
	position: fixed;
}

.showChange {
	transform: translateY(150%);
}
